.wrapper {
    width: 40%;
    height: 60%;
    background: transparent;
    border: 2px solid rgba(44, 44, 44, 0.5);
    backdrop-filter: blur(20px);
    box-shadow: 0 0 10px rgba(0, 0, 0, .8);
    /* background: #F4E0D5; */
    border-radius: 20px;
    padding: 3%;
    padding-top: 3%;
    /* color: #fff; */
    margin-top: 10%;
    margin-left: 15%;
    color: #ed7c68;

}

@media screen and (max-width: 600px) {
    .wrapper {
        width: 70%;
        height: 55%;
        padding: 8%;
        padding-top: 15%;
        margin-top: 30%;
        margin-left: center;
    }
}

@media screen and (max-height: 600px) {
    .wrapper {
        width: 40%;
        height: 70%;
        margin-top: 5%;
    }
}

.wrapper h1 {
    text-align: center;
    color: #EAAEA4;

}

@media screen and (max-width: 600px) {
    .box {
        margin-top: 15%;
        margin-bottom: 8%;
    }
}

.wrapper .inp-box {
    width: 100%;
    height: 8%;
    /* background: orange; */
    margin: 3% 0;
}

@media screen and (max-width: 600px) {
    .wrapper .inp-box {
        margin: 5% 0;
        /* height: 5%; */
    }
}

@media screen and (max-height: 600px) {
    .wrapper .inp-box {
        height: 5%;
    }
}

/* how to save responsiveness for diffent different devices in css? */
.inp-box input {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    background: transparent;
    /* border: none; */
    background: #F4E0D5;

    outline: none;
    border: 1.5px solid rgba(0, 0, 0, 0.5);
    border-radius: 40px;
    font-size: medium;
    color: #000;
    padding: 3%;
}

@media screen and (max-width: 600px) {
    .inp-box input {
        /* padding: 1.5%; */
        padding-left: 5%;
    }
}

@media screen and (max-height: 600px) {
    .inp-box input {
        padding: 1.8%;
        padding-left: 5%;
    }
}

