.wrp{
    width: 40%;
    height: 60%;
    background: transparent;
    border: 2px solid rgba(44, 44, 44, 0.5);
    backdrop-filter: blur(20px);
    box-shadow: 0 0 10px rgba(0, 0, 0, .8);
    /* background: #F4E0D5; */
    border-radius: 20px;
    padding: 3%;
    padding-top: 3%;
    /* color: #fff; */
    margin-top: 10%;
    margin-left: 15%;
    color: #ed7c68;
}

@media screen and (max-width: 600px) {
    .wrp {
        width: 70%;
        height: 50%;
        padding: 8%;
        padding-top: 15%;
        margin-top: 30%;
        margin-left: center;
    }
}

@media screen and (max-height: 600px) {
    .wrp {
        width: 40%;
        height: 70%;
        margin-top: 5%;
    }
}

.con{
    margin-top: 10%;
}
@media screen and (max-height: 600px) {
    .con {
        margin-top: 10%;
    }
}
.bod{
    margin-top: 18%;
}
@media screen and (max-width: 600px) {
